export function getTehsil(city: string): string[] {
    var cityName = city.toUpperCase();
    var talukaList: string[];
    switch (cityName) {
        case "AHMEDABAD":
            talukaList = ahmedabad;
            break;
        case "AMRELI":
            talukaList = amreli;
            break;
        case "ANAND":
            talukaList = anand;
            break;
        case "BANASKANTHA":
            talukaList = banaskantha;
            break;
        case "BHARUCH":
            talukaList = bharuch;
            break;
        case "BHAVNAGAR":
            talukaList = bhavnagar;
            break;
        case "DAHOD":
            talukaList = dahod;
            break;
        case "DANG":
            talukaList = dang;
            break;
        case "GANDHINAGAR":
            talukaList = gandhinagar;
            break;
        case "JAMNAGAR":
            talukaList = jamnagar;
            break;
        case "JUNAGADH":
            talukaList = junagadh;
            break;
        case "KHEDA":
            talukaList = kheda;
            break;
        case "KUTCH":
            talukaList = kutch;
            break;
        case "MEHSANA":
            talukaList = mehsana;
            break;
        case "NARMADA":
            talukaList = narmada;
            break;
        case "NAVSARI":
            talukaList = navsari;
            break;
        case "PANCHMAHAL":
            talukaList = panchmahal;
            break;
        case "PATAN":
            talukaList = patan;
            break;
        case "PORBANDAR":
            talukaList = porbandar;
            break;
        case "RAJKOT":
            talukaList = rajkot;
            break;
        case "SABARKANTHA":
            talukaList = sabarkantha;
            break;
        case "SURAT":
            talukaList = surat;
            break;
        case "SURENDRANAGAR":
            talukaList = surendranagar;
            break;
        case "TAPI":
            talukaList = tapi;
            break;
        case "VADODARA":
            talukaList = vadadora;
            break;
        case "VALSAD":
            talukaList = valsad;
            break;
    }
    return talukaList;
}





