import { Component, OnInit, ViewChild } from "@angular/core";
import { FormControl, FormGroup, Validators, ReactiveFormsModule } from "@angular/forms";
import { MatProgressBar } from "@angular/material";
import { Router } from "@angular/router";
import { cityList, amreli, getTehsil, } from "../data/city.data";
import { AppLoaderService } from "./../../shared/services/app-loader/app-loader.service";
import { MatSelectModule } from '@angular/material/select';
import { VolunteerService } from "../volunteer.service";
import { VolunteerModel } from "../models/volunteer";

@Component({
  selector: 'app-add-volunteer',
  templateUrl: './add-volunteer.component.html',
  styleUrls: ['./add-volunteer.component.scss']
})
export class AddVolunteerComponent implements OnInit {
  @ViewChild(MatProgressBar)
  progressBar: MatProgressBar;
  districts: string[]
  tehsils: string[]
  formData = {}
  console = console
  basicForm: FormGroup
  isSaved = false
  volunteer: VolunteerModel
  district: string
  tehsil: string

  constructor(
    private volunteerService: VolunteerService,

    private router: Router,
    private loader: AppLoaderService
  ) {
    this.districts = cityList;
  }

  ngOnInit() {

    this.basicForm = new FormGroup({
      serialNo: new FormControl("", [Validators.required,]),
      firstName: new FormControl("", [Validators.required]),
      lastName: new FormControl("", [Validators.required]),
      designation: new FormControl("", [Validators.maxLength(16)]),
      description: new FormControl(),
      category: new FormControl("", [Validators.required]),
      phone1: new FormControl("", [Validators.required, Validators.maxLength(11)]),
      phone2: new FormControl("", [Validators.maxLength(11)]),
      phone3: new FormControl("", [Validators.maxLength(11)]),
      phone4: new FormControl("", [Validators.maxLength(11)]),
      email1: new FormControl("", [Validators.email]),
      email2: new FormControl("", [Validators.email]),

      addressLine1: new FormControl("", [Validators.maxLength(70)]),
      addressLine2: new FormControl("", [Validators.maxLength(70)]),
      addressLine3: new FormControl("", [Validators.maxLength(70)]),

      tehsilData: new FormControl("", [Validators.required]),
      districtData: new FormControl("", [Validators.required]),

    });
  }

  changeDistrict(data) {
    console.log("District has been selected")
    console.log(data)
    this.tehsils = getTehsil(data)
    this.district = data
  }
  changeTehsil(data) {
    console.log("Tehsil has been selected")
    console.log(data)
    this.tehsil = data
  }
  onSubmit() {
    this.volunteer = new VolunteerModel()
    this.progressBar.mode = "indeterminate";

    this.volunteer.serialNo = this.basicForm.value["serialNo"]
    this.volunteer.firstName = this.basicForm.value["firstName"]
    this.volunteer.lastName = this.basicForm.value["lastName"]
    this.volunteer.designation = this.basicForm.value["designation"]
    this.volunteer.description = this.basicForm.value["description"]
    this.volunteer.category = this.basicForm.value["category"]
    this.volunteer.phone1 = this.basicForm.value["phone1"]
    this.volunteer.phone2 = this.basicForm.value["phone2"]
    this.volunteer.phone3 = this.basicForm.value["phone3"]
    this.volunteer.phone4 = this.basicForm.value["phone4"]
    this.volunteer.email1 = this.basicForm.value["email1"]
    this.volunteer.email2 = this.basicForm.value["email2"]

    this.volunteer.addressLine1 = this.basicForm.value["addressLine1"]
    this.volunteer.addressLine2 = this.basicForm.value["addressLine2"]
    this.volunteer.addressLine3 = this.basicForm.value["addressLine3"]

    this.volunteer.tehsil = this.tehsil
    this.volunteer.district = this.district

    this.isSaved = true; // it is time to save some data    


    if (this.isSaved) {
      this.volunteerService
        .createVolunteer(this.volunteer)
        .then(() => {
          this.loader.open("Creating Volunteer");
          setTimeout(() => {
            this.loader.close();
          }, 1000);
          this.basicForm.reset()
          this.basicForm.pristine
          this.progressBar.mode= "determinate"
        })
        .catch(() => {
          this.progressBar.mode = null;
          this.isSaved = false;
        });
    }

  }

}
