import { Component, OnInit } from '@angular/core';
import { VolunteerModel } from '../models/volunteer';
import { VolunteerService } from '../volunteer.service';
import { Router, NavigationExtras } from "@angular/router";

@Component({
  selector: 'app-view-volunteer',
  templateUrl: './view-volunteer.component.html',
  styleUrls: ['./view-volunteer.component.scss']
})
export class ViewVolunteerComponent implements OnInit {
  volunteerList: VolunteerModel[]
  rows = [];
  columns = [];
  temp = [];
  selected = [];

  constructor(private volunteerService: VolunteerService, private router: Router) { }

  ngOnInit() {
    this.volunteerService.getAllVolunteers().subscribe(volunt => {
      this.volunteerList = volunt
      this.rows = this.temp = this.volunteerList
    })

    this.columns = [
      {
        prop: 'serialNo',
        name: 'Serial No'
      },
      {
        prop: 'firstName',
        name: 'First Name'
      },
      {
        prop: 'lastName',
        name: 'Last Name'
      },
      {
        prop: 'category',
        name: 'Category'
      },
      {
        prop: 'district',
        name: 'District'
      },
      {
        prop: 'tehsil',
        name: 'Tehsil'
      },
      {
        prop: 'phone1',
        name: 'Phone'
      },

    ];
  }

  updateFilter(event) {
    // console.log("--- event is fired ---")
    // console.log(event.target.value.toLowerCase())
    const val = event.target.value.toLowerCase();
    var columns = Object.keys(this.temp[0]);
    // Removes last "$$index" from "column"
    columns.splice(columns.length - 1);

    // console.log(columns);
    if (!columns.length)
      return;

    const rows = this.temp.filter(function (d) {
      for (let i = 0; i <= columns.length; i++) {
        let column = columns[i];
        // console.log(d[column]);
        if (d[column] && d[column].toString().toLowerCase().indexOf(val) > -1) {
          return true;
        }
      }
    });
    this.rows = rows;
  } // updateFilter -- closes here

  onSelect({ selected }) {
    console.log("A volunteer has been selected")
    console.log(selected[0].docId)
    console.log(selected[0].firstName)
    const navigationExtras: NavigationExtras = {
      queryParams: {
        docId: selected[0].docId
      }
    }
    this.router.navigate(['edit_volunteer'], navigationExtras);
  }
}

